.c-payment-modal {
  position: relative;
  background-color: $s-color--background;
  height: 100%;
  opacity: 0;
  z-index: -1000;
  visibility: hidden;
  pointer-events: none;
  position: fixed;
  transition: opacity .2s;
  height: 0;
  display: none;
  color: $s-color--btn-font-alt;
  text-align: left;

  &.is-visible {
    inset: 0;
    opacity: 1;
    z-index: $s-z--cart-drawer;
    visibility: visible;
    pointer-events: all;
    width: 100%;
    height: 100%;
    display: flex;
  }

  &__inner {
    width: 100%;
    max-width: 37.625rem;
    margin: 0 auto;
    position: relative;
    background-color: $s-color--background;
  }

  &__close {
    position: absolute;
    top: 0;
    right: 0;
    padding: 1.5rem 1.375rem 0 0;
    cursor: pointer;
  }

  &__close-icon {
    width: 1.625rem;
    height: 1.625rem;
  }

  &__tabs {
    display: flex;
    position: relative;
    width: 100%;
    padding-left: 1.75rem;
    align-items: baseline;
    z-index: $s-z--text-overlay;
    margin-top: 3.8125rem;
    overflow-x: auto;
    scroll-behavior: smooth;
    scrollbar-width: none;

    &::-webkit-scrollbar {
      display: none;
    }

    &-container {
      position: relative;

      &::after {
        position: absolute;
        top: 0;
        display: block;
        right: 0;
        width: 4rem;
        height: 100%;
        background: linear-gradient(to right, rgba($s-color--background, 0), rgba($s-color--background, 1));
        z-index: $s-z--text-overlay;
        pointer-events: none;
        content: "";
      }
    }

  }

  &__tab-blob {
    position: absolute;
    z-index: $s-z--reset;
    width: 33%;
    top: -0.625rem;
    left: 0;
    transition: left 0.2s;
    display: block;
  }

  &__tab {
    margin-right: 1rem;
    color: $s-color--font-focus;
    z-index: $s-z--text-overlay;
    cursor: pointer;

    &.is-active {
      text-decoration: underline;
    }
  }

  &__contents {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
    padding: 2.75rem 1.75rem 1.75rem;
  }

  &__content {
    opacity: 0;
    visibility: hidden;
    grid-row: 1/-1;
    grid-column: 1/-1;
    padding: 0 0.5rem 2.75rem;
    display: block;

    &.is-visible {
      opacity: 1;
      visibility: visible;
    }

    p {
      @include t-font--paragraph-2;

      strong {
        @include t-font--paragraph-1;
      }
    }
  }

  @include t-media-query($s-small-up) {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: $s-color--background-subdued;

    &__close {
      padding: 2.5rem 2.5rem 0 0;
    }

    &__inner {
      padding: 4.125rem 2.8125rem;
    }

    &__tabs {
      margin: 0;
      padding: 0;

      &-container::after {
        display: none;
      }
    }

    &__contents {
      padding: 0;
    }

    &__content {
      padding: 3.3125rem 0 2.75rem;
    }

    &__tab {
      font-size: 1.125rem;
    }
  }

  @include t-media-query($s-medium-up) {
   &__content p strong {
      @include t-font--paragraph-1-laptop;
    }
  }

  @include t-media-query($s-xlarge-up) {
     &__content p strong {
      @include t-font--paragraph-1-desktop;
    }
  }
}
