.c-product-image-carousel {
  overflow: hidden;

  &__slides {
    display: flex;

    &.has-one-image {
      justify-content: center;
    }
  }

  &__slide {
    cursor: pointer;
    flex-shrink: 0;
    width: 100%;

    .is-initialised & {
      width: auto;
    }
  }

  &__dots {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: .5rem;
    padding-top: 1.625rem;
  }

  &__nav-container {
    display: none;
  }


  @include t-media-query($s-small-up) {
    max-width: 34.875rem;
    margin: 0 auto;

    &__nav-container {
      @include t-absolute-center-x-y();
      display: flex;
      justify-content: space-between;
      width: 46.0625rem;
      top: 37%;
    }

    &__arrow {
      display: block;
      background: rgba($s-color--background, .5);
      border-radius: 3.125rem;
      padding: 1rem;
      cursor: pointer;

      &-icon {
        width: 1rem;
        height: 1rem;
        display: block;
      }
    }

  }

  @include t-media-query($s-medium-up) {
    position: initial;

    &__arrow-icon {
      width: 2rem;
      height: 2rem;
      display: block;
    }


    &__dots {
      padding-top: 2rem;
      gap: 1.5rem;
    }
  }
}