#smartwishlist.c-product__wishlist {
  display: flex;
  height: auto;
  min-height: 1.375rem;
  margin: 0;
}

#bookmarkit.unbookmarked.sw_tooltip.tooltip.swpbutton2 {
  height: auto;
}

#sw_wishlist_label {
  font-size: 1rem !important;
  color: $s-color--font;
  line-height: 1;
}

#wishlist_icon.fa {
  font-size: 1rem !important;

  &:before {
    content: "" !important;
    width: 1.25rem;
    height: 1.25rem;
    display: inline-block;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 100%;
    background-image: url(../../assets/icon-heart.svg);
    transition: .2s;
  }

  .bookmarked & {
    &:before {
      background-image: url(../../assets/icon-heart-hover.svg);
    }
  }
}

div#bookmarkit.swpbutton2 {
  display: flex;
  align-items: center;
  height: auto;

  &:hover {
    #wishlist_icon.fa:before {
      background-image: url(../../assets/icon-heart-hover.svg);
    }
  }
}

#sw_wishlist_label {
  margin-left: 0.5rem;
}

.bookmarked {
  color: $s-color--font-focus !important;
}

@include t-media-query($s-medium-up) {
  #sw_wishlist_label {
    margin-left: 0.625rem;
  }

  #sw_wishlist_label {
    font-size: 1.125rem !important;
  }

  #wishlist_icon.fa {
    font-size: 1rem !important;

    &:before {
      content: "" !important;
      width: 1.75rem;
      height: 1.75rem;
    }
  }
}