.c-product-details {
  background-color: $s-color--background-subdued;

  &__outer {
    max-width: $s-page-wrap--xlarge;
    margin: 0 auto;
    padding: 2.1875rem 0 2.125rem;
  }

  &__inner {
    padding: 0 $s-gutter--edge-mobile;
  }

  &__description {
    text-align: center;
    margin: 0 auto 1.6875rem;
    max-width: 60ch;
  }

  &__dimensions {
    text-align: center;
    margin-bottom: 0.5rem;
  }

  &__featured-blob-wrapper {
    position: relative;
    width: 100%;
    min-height: 15.75rem;
    padding-bottom: 1rem;
    max-width: 20.9375rem;
    margin: 0 auto;
  }

  &__featured-blob-one {
    width: 8.5rem;
    height: 8.5rem;
    top: 0;
    left: 0.5rem;
    position: absolute;
  }

  &__featured-blob-two {
    width: 12.5rem;
    height: 12.5rem;
    position: absolute;
    bottom: 0;
    right: 0;

    &.is-desktop {
      display: none;
    }

    &.no-blob-one {
      inset: revert;
      @include t-absolute-center-x-y();
    }
  }

  &__blob-one-content {
    inset: 0;
    text-align: center;
    display: grid;
    grid-gap: 0.375rem;
    justify-items: center;
    align-content: center;
    color: $s-color--font-invert;
    width: 80%;
    @include t-absolute-center-x-y();
    top: 51%;
  }

  &__blob-one-heading {
    margin-bottom: 0;
  }

  &__blob-one-description {
    margin-bottom: 0;
    font-size: 0.875rem;
    line-height: 1.333;
  }

  &__blob-two-content {
    position: absolute;
    inset: 0;
    text-align: center;
    display: grid;
    justify-items: center;
    align-content: center;
    grid-gap: 2%;
    color: $s-color--font-invert;
    width: 80%;
    @include t-absolute-center-x-y();
    top: 54%;
  }

  &__blob-two-icon {
    width: 2.1875rem;
    height: 2.1875rem;
    margin-bottom: 0.375rem;
  }

  &__blob-two-heading {
    margin-bottom: 0.375rem;
  }

  &__blob-two-description {
    margin-bottom: 0;
    max-width: 13ch;
  }

  &__featured-image-wrapper {
    position: relative;
    height: 91vw;
    margin: 0 auto 2.375rem;
  }

  &__featured-image-wrapper-one {
    position: absolute;
    top: 0.6875rem;
    left: 0;
    width: 47%;
    height: auto;
  }

  &__featured-image-container-one {
    padding-bottom: 130%;
    min-width: 9.875rem;
    height: 100%;
  }

  &__featured-image-wrapper-two {
    position: absolute;
    bottom: 0;
    right: -0.3125rem;
    width: 56%;
  }

  &__featured-image-container-two {
    padding-bottom: 125%;
    min-width: 11.75rem;
  }

  &__featured-image-icon {
    z-index: 2;
    width: 18%;
    @include t-absolute-center-x-y;
    transform: translate(-50%, -50%) rotate(9deg);
    top: 68%;
    left: 38%;
  }

  &__dimension {
    font-size: 0.9375rem;
    font-weight: 400;
    text-align: center;
  }

  &__accordion {
    &[open] > &-heading {
      &::before {
        opacity: 0;
        transform: translateY(-50%) rotate(90deg);
      }
    }
  }

  .o-accordion {
    &__content,
    &__content p {
      @include t-font--paragraph-2;
    }

    &__content a {
      @include t-font--link;
      position: relative;
      padding-bottom: 0.0625rem;
      cursor: pointer;
      color: $s-color--font-focus;

      &::before {
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        height: 0.0625rem;
        background: $s-color--border-focus;
        content: "";
      }
    }

    &__heading {
      color: $s-color--font-focus;
    }
  }

  &__left-image-blob-wrapper {
    display: none;
  }

  &__right-image-wrapper {
    display: none;
  }

  &__tab-blob {
    display: none;
  }

  @include t-media-query($s-small-up) {
    &__blob-image-wrapper {
      position: relative;
      padding: 9.375rem 5rem 2rem 5rem;
    }

    &__featured-image-wrapper {
      height: 64vw;
    }

    &__featured-image-wrapper-twp {
      right: 3%;
    }

    &__featured-blob-wrapper {
      position: initial;
      min-height: initial;
      margin: 0;
      padding: 0;
    }

    &__featured-blob-one {
      z-index: 2;
      left: 20%;
      top: initial;
      bottom: 3rem;
    }

    &__featured-blob-two {
      z-index: 2;
      bottom: initial;
      top: 1.375rem;
      right: 22%;
    }

    &__featured-image-icon {
      top: 74%;
      left: 43%;
    }

    &__description {
      margin-top: 3rem;
    }
  }

  @include t-media-query($s-medium-up) {
    position: relative;

    &__outer {
      padding: 5.5rem 0 5.625rem;
    }

    &__inner {
      padding: 0 6%;
    }

    &__blob-image-wrapper {
      display: none;
    }

    &__accordions {
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      justify-content: space-between;
      max-width: 54.5rem;
      margin: 1.875rem auto 0;
      position: relative;

      &.has-1-tabs {
        grid-template-columns: repeat(1, 1fr);
      }

      &.has-2-tabs {
        grid-template-columns: repeat(2, 1fr);

        .o-accordion {
          &:nth-of-type(2) {
            .o-accordion__content {
              transform: translateX(-50%);
            }
          }
        }
      }

      &.has-3-tabs {
        grid-template-columns: repeat(3, 1fr);

        .o-accordion {
          &:nth-of-type(2) {
            .o-accordion__content {
              transform: translateX(-33%);
            }
          }
          &:nth-of-type(3) {
            .o-accordion__content {
              transform: translateX(-66.6%);
            }
          }
        }
      }
    }

    .o-accordion {
      border: none;
      min-width: revert;
      max-width: revert;
      z-index: 2;
      overflow: visible;

      &[open] {
        .o-accordion__content {
          opacity: 0;
          transition: all 0.2s;
          margin-top: 2rem;
          overflow: visible;
        }

        .o-accordion__heading {
          cursor: pointer;
        }

        .o-accordion__label {
          transition: all 0.2s;
        }
      }

      &:nth-of-type(1) {
        .o-accordion__content {
          transform: translateX(0);
        }
      }

      &:nth-of-type(2) {
        .o-accordion__content {
          transform: translateX(-25%);
        }
      }

      &:nth-of-type(3) {
        .o-accordion__content {
          transform: translateX(-50%);
        }
      }

      &:nth-of-type(4) {
        .o-accordion__content {
          transform: translateX(-75%);
        }
      }

      &__content {
        position: absolute;
        left: 0;
        max-width: 54.5rem;
        @include t-absolute-center-x();
        text-align: center;
        opacity: 0;
        transition: opacity 0.2s;
        width: 400%;
        left: 0;
        position: relative;
      }

      &__heading {
        padding-bottom: 3.125rem;

        &::before,
        &::after {
          content: none;
        }

        &.is-active {
          .o-accordion__tab-blob {
            opacity: 1;
          }
        }
      }
    }

    &__tab-blob {
      display: block;
      transition: all 0.2s;
      position: absolute;
      top: 0;
      left: 0;
      max-width: 25%;
      z-index: 0;

      .has-3-tabs & {
        left: 3%;
      }

      .has-2-tabs & {
        left: 11%;
      }

      .has-1-tabs & {
        left: 37%;
      }
    }

    .o-accordion[open].is-active {
      z-index: $s-z--illustration-overlay;
      position: relative;
      .o-accordion__label {
        color: $s-color--font-invert;
      }

      .o-accordion__content {
        opacity: 1;
        z-index: $s-z--illustration-overlay;
        position: relative;
      }
    }

    &__description {
      max-width: 49ch;
      margin-top: 0;
    }

    &__left-image-blob-wrapper {
      position: absolute;
      width: 14.7%;
      top: -13.3125rem;
      display: block;

      &.has-no-image {
        left: 0;
        top: 2.3125rem;
      }
    }

    &__featured-image-wrapper-one {
      min-width: 9.6875rem;
      width: 100%;
      position: relative;
      top: initial;
    }

    &__featured-blob-one {
      width: 75%;
      height: auto;
      max-height: 11.5625rem;
      max-width: 11.5625rem;
      min-width: 6.875rem;
      left: 75%;
      bottom: -6.75rem;
    }

    &__featured-image-wrapper-two {
      min-width: 9.6875rem;
      width: 100%;
      position: relative;
      bottom: initial;
      right: initial;
    }

    &__right-image-wrapper {
      position: absolute;
      width: 18.45%;
      right: 7.8%;
      top: -8.875rem;
      display: block;
    }

    &__featured-image-icon {
      width: 55%;
      bottom: -5.5rem;
      right: -22%;
      top: initial;
      left: initial;
      transform: rotate(-9deg);
    }

    &__featured-blob-two.is-desktop {
      display: block;
      width: clamp(13rem, 14%, 18rem);
      height: auto;
      top: 10.375rem;
      right: 7%;
    }

    &__blob-two-icon {
      width: 3.375rem;
      height: 2.6875rem;
    }
  }

  @include t-media-query($s-large-up) {
    &__featured-blob-two.is-desktop {
      top: 40.25rem;
      right: 7%;
    }

    &__featured-blob-one {
      width: 63%;
    }

    &__left-image-blob-wrapper.has-no-image {
      left: 0;
      top: 6.75rem;
    }
  }

  @include t-media-query($s-xlarge-up) {
    &__blob-one-description {
      font-size: 1.125rem;
    }

    &__blob-two-content {
      width: 85%;
    }

    &__blob-two-icon {
      width: 3.375rem;
      height: 2.6875rem;
    }

    &__featured-blob-two.is-desktop {
      right: 11%;
    }

    &__featured-blob-one {
      width: 75%;
    }
  }

  @include t-media-query($s-xxlarge-up) {
    &__blob-two-icon {
      width: 4rem;
      height: 3.5rem;
    }

    &__featured-blob-two.is-desktop {
      top: 40.25rem;
      right: 15%;
    }

    &__featured-blob-one {
      width: 63%;
    }

    &__left-image-blob-wrapper {
      top: -23.625rem;
    }

    &__right-image-wrapper {
      top: -5.9375rem;
    }

    &__outer {
      position: relative;
    }
  }
}
