/*
 *  Quantity Selector
 *    Styles for the quantity selector
 *
 *  Version:
 *    1.0.0 - 2020/11/29
 */

.o-quantity-selector {
  &__inner {
    display: flex;
    justify-content: space-around;
    width: 8.5625rem;
    border-radius: 6.25rem;
    overflow: hidden;
    background: $s-color--background-subdued;
  }

  &__input {
    &-field {
      width: 2rem;
      padding: 0;
      height: 100%;
      text-align: center;
      font-size: 1rem;
      background-color: transparent;
      -moz-appearance: textfield;
      &::-webkit-outer-spin-button,
      &::-webkit-inner-spin-button {
        -webkit-appearance: none;
        margin: 0;
      }
    }
  }

  &__button {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 2rem;
    padding: 1rem 1.5rem;
    cursor: pointer;

    &.is-disabled {
      cursor: not-allowed;
      pointer-events: none;
    }
  }

  &__icon {
    width: 0.625rem;
    height: 0.625rem;
    color: $s-color--font;

    .is-disabled & {
      color: $s-color--font-50;
    }
  }

  /*** Media Queries ***/

  @include t-media-query($s-xlarge-up) {
    &__inner {
      width: 10.1875rem;
      justify-content: center;
    }
    &__button {
      padding: 1.3125rem 1.5rem;
      &:active {
        border-color: transparent;
      }
    }

    &__icon {
      width: 1rem;
      height: 1rem;
    }

    &__input-field{
      font-size: .875rem;
    }
  }
}
