.c-product-carousel-dot {
  display: inline-block;
  height: .25rem;
  width: .25rem;
  border-radius: 50%;
  cursor: pointer;
  background-color: $s-color--btn-background;
  display: block;
  padding: 0.25rem;

  &.is-active {
    background-color: $s-color--font;
    transition: all .2s;

    .c-product-carousel-dot__stroke svg path {
      stroke: $s-color--btn-background;
    }
  }

  @include t-media-query($s-small-up) {
    position: relative;
    width: 3.75rem;
    height: 3.75rem;
    display: block;
    background-color: transparent;
    background: url(../../assets/svg.carousel-dot.svg) no-repeat center center/100%;

    &.is-active {
      background-color: transparent;
      transition: all .2s;
      background: url(../../assets/svg.carousel-dot-active.svg) no-repeat center center/100%;
    }

    &__image {
      mask-image: url(../../assets/svg.carousel-dot.svg);
      mask-repeat: no-repeat;
      mask-size: 100%;
      mask-position: center;
    }
  }
}