.c-product {
  max-width: $s-page-wrap--xlarge;
  margin: 0 auto;

  &__inner {
    padding: 2.125rem $s-gutter--edge-mobile;
  }

  &__main {
    position: relative;
    display: grid;
  }

  &__preheading {
    text-align: center;
    text-transform: capitalize;
    margin-bottom: 0.625rem;
    color: $s-color--font-focus;
  }

  &__title {
    text-align: center;
  }

  &__pricing {
    @include t-font--heading-5;
    text-align: center;
    margin-top: 1.625rem;
    margin-bottom: 0.75rem;
    color: $s-color--font-focus;

    .o-product-pricing__compare {
      display: none;
      color: $s-color--font-50;
      text-decoration: line-through;
      margin-right: 1rem;
    }

    &.has-compare .o-product-pricing__compare {
      display: inline-block;
    }
  }

  .jdgm-preview-badge {
    margin-bottom: 0.9375rem;
    text-align: center;

    .jdgm-star {
      color: map-deep-get($brand-colors, "orange", "primary");
    }

    .jdgm-prev-badge__text {
      display: none;
    }
  }

  &__wishlist {
    display: inline-flex;
    justify-content: center;
    gap: 0.625rem;
    cursor: pointer;
    margin: 0 auto;

    &:hover &-icon {
      fill: $s-color--btn-background;
      transition: all 0.2s;
    }
  }

  &__wishlist-icon {
    width: 1.25rem;
    height: 1.25rem;
    fill: none;
    transition: all 0.2s;
  }

  &__wishlist-container {
    min-height: 1.375rem;
    margin-top: 1rem;
  }

  &__payment {
    &-options {
      margin-bottom: 1.125rem;
      text-align: center;
      color: rgba($s-color--font, 0.4);

      &-label {
        margin-bottom: 0.75rem;
      }
    }

    &-logos {
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 1rem;
    }

    &-logo {
      display: inline-flex;
      justify-content: center;
      align-items: center;
      height: 1.125rem;
      opacity: 0.5;

      svg {
        width: 100%;
        height: 100%;
        max-height: 0.875rem;

        * {
          fill: $s-color--background-overlay;
        }
      }
    }
  }

  &__purchase {
    &-wrapper {
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 0 0 2.1875rem;
      gap: 0.625rem;

      .o-quantity-selector,
      .o-quantity-selector__inner {
        height: 100%;
      }
    }
  }

  &__add-btn {
    width: 100%;
    max-width: 11.8125rem;
    transition: background-color .2s;
    @include t-anim--transition(opacity, $ease: linear);

    &:not(.is-disabled):hover {
      opacity: 0.9;
    }

    &.is-disabled {
      background-color: $s-color--btn-background-disable;
    }

    &.is-added{
      background-color: $s-color--btn-background-added;
    }
  }

  .o-swatches {
    &__variant-selector-wrapper {
      margin: 1.5rem auto;
      position: relative;
      max-width: 18.5rem;
      width: 100%;

      &::after {
        content: "";
        display: block;
        position: absolute;
        height: 0.875rem;
        width: 0.875rem;
        top: 50%;
        right: 1.25rem;
        background-image: url("./../../assets/icon-arrow-down.svg");
        background-repeat: no-repeat;
        background-position: center;
        transform: translateY(-50%);
        pointer-events: none;
        z-index: 5;
      }
    }

    &__variant-selector {
      background-color: $s-color--background;
      padding: 1rem 2.5rem 1rem 1.25rem;
      border: 0.0625rem solid $s-color--border-focus;
      border-radius: 0.25rem;
      outline: none;
      width: 100%;
      color: $s-color--font;
      opacity: 0.9;

      &:focus {
        border: 0.0625rem solid $s-color--border-focus;
        border-radius: 0.25rem;
      }
    }
  }

  @include t-media-query($s-medium-up) {
    position: relative;
    padding-top: 3.75rem;

    &__pricing {
      @include t-font--heading-5-laptop;
    }

    &__payment-logo {
      cursor: pointer;
    }
  }

  @include t-media-query($s-xlarge-up) {
    &__pricing {
      @include t-font--heading-5-desktop;
      margin-top: 1.0625rem;
    }

    .jdgm-preview-badge {
      margin-bottom: 2.125rem;
    }

    &__purchase-wrapper {
      gap: 1rem;
    }

    &__add-btn {
      width: auto;
      max-width: none;
      min-width: 15.125rem;
    }

    &__payment {
      &-options {
        margin-bottom: 2rem;

        &-label {
          margin-bottom: 0.5rem;
        }
      }

      &-logo {
        svg {
          max-height: 1.0625rem;
        }
      }
    }

    &__purchase-wrapper {
      gap: 1rem;
    }

    &__add-btn {
      width: auto;
      max-width: none;
      min-width: 15.125rem;
    }

    &__payment {
      &-options {
        margin-bottom: 2rem;

        &-label {
          margin-bottom: 0.5rem;
        }
      }

      &-logo {
        svg {
          max-height: 1.0625rem;
        }
      }
    }
  }
}